import axios from "axios";  

const databaseApiUrl = process.env.VUE_APP_DATABASE_API_URL;
let headers = {
  "Content-Type": "application/json",
  "Accept":'application/json',
  "ngrok-skip-browser-warning": "true",
}

// const databaseApiUrl = 'http://localhost:8000/api';
/**
 * –ó–∞–≤–∞–Ω—Ç–∞–∂—É—î —Å–ø–∏—Å–æ–∫ –¥–∞–Ω–∏—Ö –∑ –±–∞–∑–∏ –¥–∞–Ω–∏—Ö –∑–∞ –¥–æ–ø–æ–º–æ–≥–æ—é HTTP GET –∑–∞–ø–∏—Ç—É.
 *
 * @param {string} name - –ù–∞–∑–≤–∞ –µ–Ω–¥–ø–æ—ñ–Ω—Ç—É –∞–±–æ —à–ª—è—Ö—É –¥–æ —Ä–µ—Å—É—Ä—Å—É –±–∞–∑–∏ –¥–∞–Ω–∏—Ö (infos/advertisements/works/users).
 * @returns {Promise<Array>} - –û–±—ñ—Ü—è–Ω–∫–∞ (Promise), —è–∫–∞ –≤–∏—Ä—ñ—à–∏—Ç—å—Å—è –º–∞—Å–∏–≤–æ–º –¥–∞–Ω–∏—Ö –æ—Ç—Ä–∏–º–∞–Ω–∏—Ö –∑ —Å–µ—Ä–≤–µ—Ä–∞.
 *                     –£—Å–ø—ñ—à–Ω–∞ –æ–±—ñ—Ü—è–Ω–∫–∞ –º—ñ—Å—Ç–∏—Ç—å –º–∞—Å–∏–≤ –¥–∞–Ω–∏—Ö, —ñ–Ω–∞–∫—à–µ –≤–æ–Ω–∞ –≤—ñ–¥—Ö–∏–ª—è—î—Ç—å—Å—è –∑ –ø–æ–º–∏–ª–∫–æ—é.
 */
export function loadItemsList(name, pageIndex = null) {
  let url;
  if (pageIndex) {
    url = `${databaseApiUrl}/${name}?page=${pageIndex}`;
  } else {
    url = `${databaseApiUrl}/${name}`;
  }
  return axios
    .get(url, {
      headers: headers,
    })
    .then((response) => {
      if (pageIndex) {
        return response.data;
      }
      return response.data.data;
    })
    .catch((error) => {
      console.error(error);
    });
}
/**
 * –ó–∞–≤–∞–Ω—Ç–∞–∂—É—î —Å–ø–∏—Å–æ–∫ –¥–∞–Ω–∏—Ö –∑ –±–∞–∑–∏ –¥–∞–Ω–∏—Ö –∑–∞ –¥–æ–ø–æ–º–æ–≥–æ—é HTTP GET –∑–∞–ø–∏—Ç—É, –æ–±–º–µ–∂–µ–Ω–∏–π –∑–∞ —Ä–æ–∑–¥—ñ–ª–æ–º –≥—É—Ä—Ç–æ–∂–∏—Ç–∫—É.
 *
 * @param {string} name - –ù–∞–∑–≤–∞ –µ–Ω–¥–ø–æ—ñ–Ω—Ç—É –∞–±–æ —à–ª—è—Ö—É –¥–æ —Ä–µ—Å—É—Ä—Å—É –±–∞–∑–∏ –¥–∞–Ω–∏—Ö (infos/advertisements/works/users).
 * @param {number} dormitory - –ù–æ–º–µ—Ä –≥—É—Ä—Ç–æ–∂–∏—Ç–∫—É, –∑–∞ —è–∫–∏–º –æ–±–º–µ–∂—É—î—Ç—å—Å—è –≤–∏–±—ñ—Ä–∫–∞ –¥–∞–Ω–∏—Ö.
 * @returns {Promise<Array>} - –û–±—ñ—Ü—è–Ω–∫–∞ (Promise), —è–∫–∞ –≤–∏—Ä—ñ—à–∏—Ç—å—Å—è –º–∞—Å–∏–≤–æ–º –¥–∞–Ω–∏—Ö –æ—Ç—Ä–∏–º–∞–Ω–∏—Ö –∑ —Å–µ—Ä–≤–µ—Ä–∞.
 *                     –£—Å–ø—ñ—à–Ω–∞ –æ–±—ñ—Ü—è–Ω–∫–∞ –º—ñ—Å—Ç–∏—Ç—å –º–∞—Å–∏–≤ –¥–∞–Ω–∏—Ö, —ñ–Ω–∞–∫—à–µ –≤–æ–Ω–∞ –≤—ñ–¥—Ö–∏–ª—è—î—Ç—å—Å—è –∑ –ø–æ–º–∏–ª–∫–æ—é.
 */
export function loadItemsListByDormitory(name, dormitory, pageIndex = null) {
  let url;
  if (pageIndex) {
    url = `${databaseApiUrl}/${dormitory}/${name}?page=${pageIndex}`;
  } else {
    url = `${databaseApiUrl}/${dormitory}/${name}`;
  }
  return axios
    .get(url, {
      headers: headers,
    })
    .then((response) => {
      if (pageIndex) {
        return response.data;
      }
      return response.data.data;
    })
    .catch(() => {
      return Promise.reject(
        "Error fetching items list. Please try again later."
      );
    });
}
/**
 * –î–æ–¥–∞—î –Ω–æ–≤–∏–π –∑–∞–ø–∏—Å —É –±–∞–∑—É –¥–∞–Ω–∏—Ö –∑–∞ –¥–æ–ø–æ–º–æ–≥–æ—é HTTP POST –∑–∞–ø–∏—Ç—É.
 *
 * @param {string} name - –ù–∞–∑–≤–∞ –µ–Ω–¥–ø–æ—ñ–Ω—Ç—É –∞–±–æ —à–ª—è—Ö—É –¥–æ —Ä–µ—Å—É—Ä—Å—É –±–∞–∑–∏ –¥–∞–Ω–∏—Ö (infos/advertisements/works/users).
 * @param {Object} data - –û–±'—î–∫—Ç –¥–∞–Ω–∏—Ö, —è–∫—ñ –±—É–¥—É—Ç—å –≤—ñ–¥–ø—Ä–∞–≤–ª–µ–Ω—ñ –Ω–∞ —Å–µ—Ä–≤–µ—Ä.
 * @param {String} token - –¢–æ–∫–µ–Ω –∫–æ—Ä–∏—Å—Ç—É–≤–∞—á–∞
 * @returns {Promise} - –û–±—ñ—Ü—è–Ω–∫–∞ (Promise), —è–∫–∞ –≤–∏—Ä—ñ—à–∏—Ç—å—Å—è —Ä–µ–∑—É–ª—å—Ç–∞—Ç–æ–º HTTP –∑–∞–ø–∏—Ç—É.
 *                     –£—Å–ø—ñ—à–Ω–∞ –æ–±—ñ—Ü—è–Ω–∫–∞ –º—ñ—Å—Ç–∏—Ç—å –æ–±'—î–∫—Ç –≤—ñ–¥–ø–æ–≤—ñ–¥—ñ —Å–µ—Ä–≤–µ—Ä–∞, —ñ–Ω–∞–∫—à–µ –≤–æ–Ω–∞ –≤—ñ–¥—Ö–∏–ª—è—î—Ç—å—Å—è –∑ –ø–æ–º–∏–ª–∫–æ—é.
 */
export function addItem(name, data, accessToken) {
  headers.Authorization = `Bearer ${accessToken}`;
  return axios
    .post(`${databaseApiUrl}/${name}`, data, {
      headers: headers,
    })
    .then((response) => {
      return response.data;
    })
    .catch((error) => {
      console.error(error);
    });
}
/**
 * –ó–∞–≤–∞–Ω—Ç–∞–∂—É—î –æ–±'—î–∫—Ç –∑ —Å–µ—Ä–≤–µ—Ä–∞ –∑–∞ –≤–∫–∞–∑–∞–Ω–æ—é –Ω–∞–∑–≤–æ—é —Ä–µ—Å—É—Ä—Å—É —Ç–∞ —ñ–¥–µ–Ω—Ç–∏—Ñ—ñ–∫–∞—Ç–æ—Ä–æ–º.
 * @param {string} name - –ù–∞–∑–≤–∞ —Ä–µ—Å—É—Ä—Å—É –¥–ª—è –∑–∞–≤–∞–Ω—Ç–∞–∂–µ–Ω–Ω—è (infos/advertisements/works/users).
 * @param {number|string} id - –Ü–¥–µ–Ω—Ç–∏—Ñ—ñ–∫–∞—Ç–æ—Ä –æ–±'—î–∫—Ç–∞ –¥–ª—è –∑–∞–≤–∞–Ω—Ç–∞–∂–µ–Ω–Ω—è.
 * @returns {Promise<Object>} - –û–±'—î–∫—Ç Promise, —è–∫–∏–π –≤–∏—Ä—ñ—à–∏—Ç—å—Å—è –æ–±'—î–∫—Ç–æ–º –∞–±–æ –≤–∏–ª—É—á–∏—Ç—å –ø–æ–º–∏–ª–∫—É.
 */
export function itemById(name, id, accessToken=null) {
  if(accessToken){
    headers.Authorization = `Bearer ${accessToken}`;
  }
  return axios
    .get(`${databaseApiUrl}/${name}/${id}`, {
      headers: headers,
    })
    .then((response) => {
      return response.data.data;
    })
    .catch((error) => {
      console.error(error);
    });
}
/**
 * –û–Ω–æ–≤–ª—é—î –æ–±'—î–∫—Ç –Ω–∞ —Å–µ—Ä–≤–µ—Ä—ñ –∑–∞ –≤–∫–∞–∑–∞–Ω–æ—é –Ω–∞–∑–≤–æ—é —Ä–µ—Å—É—Ä—Å—É —Ç–∞ —ñ–¥–µ–Ω—Ç–∏—Ñ—ñ–∫–∞—Ç–æ—Ä–æ–º.
 * @param {string} name - –ù–∞–∑–≤–∞ —Ä–µ—Å—É—Ä—Å—É –¥–ª—è –æ–Ω–æ–≤–ª–µ–Ω–Ω—è.
 * @param {number|string} id - –Ü–¥–µ–Ω—Ç–∏—Ñ—ñ–∫–∞—Ç–æ—Ä –æ–±'—î–∫—Ç–∞ –¥–ª—è –æ–Ω–æ–≤–ª–µ–Ω–Ω—è.
 * @param {Object} data - –î–∞–Ω—ñ, —è–∫—ñ —Å–ª—ñ–¥ –æ–Ω–æ–≤–∏—Ç–∏.
 * @returns {Promise<Object>} - –û–±'—î–∫—Ç Promise, —è–∫–∏–π –≤–∏—Ä—ñ—à–∏—Ç—å—Å—è –æ–Ω–æ–≤–ª–µ–Ω–∏–º –æ–±'—î–∫—Ç–æ–º –∞–±–æ –≤–∏–ª—É—á–∏—Ç—å –ø–æ–º–∏–ª–∫—É.
 */
export function updateItem(name, id, data, accessToken) {
  headers.Authorization = `Bearer ${accessToken}`;
  return axios
    .put(`${databaseApiUrl}/${name}/${id}`, data, {
      headers: headers,
    })
    .then((response) => {
      return response.data;
    })
    .catch((error) => {
      console.error(error);
    });
}
/**
 * –í–∏–¥–∞–ª—è—î –∑–∞–ø–∏—Å –∑ –±–∞–∑–∏ –¥–∞–Ω–∏—Ö –∑–∞ –¥–æ–ø–æ–º–æ–≥–æ—é HTTP DELETE –∑–∞–ø–∏—Ç—É.
 *
 * @param {string} name - –ù–∞–∑–≤–∞ –µ–Ω–¥–ø–æ—ñ–Ω—Ç—É –∞–±–æ —à–ª—è—Ö—É –¥–æ —Ä–µ—Å—É—Ä—Å—É –±–∞–∑–∏ –¥–∞–Ω–∏—Ö (infos/advertisements/works/users).
 * @param {number|string} id - –Ü–¥–µ–Ω—Ç–∏—Ñ—ñ–∫–∞—Ç–æ—Ä –æ–±'—î–∫—Ç–∞ –¥–ª—è –≤–∏–¥–∞–ª–µ–Ω–Ω—è.
 * @returns {Promise} - –û–±—ñ—Ü—è–Ω–∫–∞ (Promise), —è–∫–∞ –≤–∏—Ä—ñ—à–∏—Ç—å—Å—è —Ä–µ–∑—É–ª—å—Ç–∞—Ç–æ–º HTTP –∑–∞–ø–∏—Ç—É.
 *                     –£—Å–ø—ñ—à–Ω–∞ –æ–±—ñ—Ü—è–Ω–∫–∞ –º—ñ—Å—Ç–∏—Ç—å –æ–±'—î–∫—Ç –≤—ñ–¥–ø–æ–≤—ñ–¥—ñ —Å–µ—Ä–≤–µ—Ä–∞, —ñ–Ω–∞–∫—à–µ –≤–æ–Ω–∞ –≤—ñ–¥—Ö–∏–ª—è—î—Ç—å—Å—è –∑ –ø–æ–º–∏–ª–∫–æ—é.
 */
export function deleteItem(name, id, accessToken) {
  headers.Authorization = `Bearer ${accessToken}`;
  return axios.delete(`${databaseApiUrl}/${name}/${id}`, {
    headers: headers,
  });
}

